@import "../../variables";

#header {
  position: sticky;
  top: 0;
  background-color: $header-color;

  #title {
    line-height: 4rem;
    display: block;
    color: $light-text;
    padding-left: 2rem;
    font-family: "Comic Sans MS", "Comic Sans", cursive;
    font-size: 2rem;
  }

  @media (max-width: 768px) {
    #title {
      line-height: 2.65rem;
      font-size: 1.33rem;
      padding-left: 1.33rem;
    }
  }
}
